@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(../../bower_components/material-design-iconfont/iconfont/MaterialIcons-Regular.eot);
  /* For IE6-8 */
  src: local('Material Icons'), local('MaterialIcons-Regular'), url(../../bower_components/material-design-iconfont/iconfont/MaterialIcons-Regular.woff2) format('woff2'), url(../../bower_components/material-design-iconfont/iconfont/MaterialIcons-Regular.woff) format('woff'), url(../../bower_components/material-design-iconfont/iconfont/MaterialIcons-Regular.ttf) format('truetype');
}
.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  /* Preferred icon size */
  display: inline-block;
  width: 1em;
  height: 1em;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  -webkit-font-feature-settings: 'liga';
     -moz-font-feature-settings: 'liga';
          font-feature-settings: 'liga';
}
[layout=row] {
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
      -ms-flex-direction: row;
          flex-direction: row;
}
.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
/**
 *  Do not remove the comments below. It's the markers used by gulp-inject to inject
 *  all your less files automatically
 */
.tagged h1 {
  font-size: 40px;
  line-height: 1;
  letter-spacing: -1px;
  text-align: center;
}
@media screen and (min-width: 460px) {
  .tagged h1 {
    font-size: 60px;
  }
}
.main md-grid-list {
  width: 96%;
  margin: 60px 2% 0;
}
.main md-grid-list div.img-container {
  width: 100%;
  height: 100%;
  background-color: #666;
}
.main md-grid-list div.img-container img {
  -webkit-transition: 0.2s;
  transition: 0.2s;
  opacity: 1;
}
@media screen and (min-width: 460px) {
  .main md-grid-list div.img-container img {
    opacity: .7;
  }
}
.main md-grid-list div.img-container img:hover {
  opacity: 1;
}
.main .footer {
  text-align: center;
  padding: 30px 0;
  border-top: 1px solid #E5E5E5;
}
.md-dialog-is-showing md-grid-list div.img-container img {
  opacity: .25;
}
.img-responsive {
  max-width: 100%;
  height: auto;
}
md-dialog.slide {
  max-height: 90vh;
  padding: 0px;
  box-shadow: none;
  background: none;
}
md-dialog.slide md-dialog-content {
  padding: 0px;
  height: 100%;
  width: 100%;
  background: none;
}
md-dialog.slide md-dialog-content img {
  max-height: 80vh;
}
.navbar.navbar-fixed-top.home {
  background-color: rgba(0, 0, 0, 0.7);
  border: none;
}
.navbar.navbar-fixed-top.home li {
  color: #000;
}
@media screen and (min-width: 460px) {
  .navbar.navbar-fixed-top.home tags-input {
    min-width: 250px;
  }
}
@media screen and (min-width: 940px) {
  .navbar.navbar-fixed-top.home tags-input {
    min-width: 450px;
  }
}
.navvy {
  position: fixed;
  width: 100%;
  top: 1%;
  z-index: 60;
}
.navvy #search {
  width: 98%;
  margin: 0 1%;
}
.navvy #search tags-input .host {
  margin: 0;
}

